import { useState } from "react";
import "./styles.css";
import Todo from "./to-do";

export default function App() {
  const [value, addvalue] = useState("");
  const [item, additem] = useState([]);

  function addList(event) {
    const inputValue = event.target.value;
    addvalue(inputValue);
  }

  function updateList(event) {
    event.preventDefault();
    additem((prev) => {
      return [...prev, value];
    });
  }

  function checked(id) {
    console.log(id);
    additem((prev) => {
      return prev.filter((value, index) => {
        return index !== id;
      });
    });
  }

  return (
    <div className="App">
      <h1>To-do List</h1>
      <input onChange={addList} type="text" value={value} />
      <button onClick={updateList}>Submit</button>
      <ul>
        {item.map((e, index) => {
          return (
            <Todo
              value={e}
              lochecked={checked}
              id={index}
              key={index}
              item={e}
            />
          );
        })}
      </ul>
    </div>
  );
}
